.nav-sticky {
  position: -webkit-sticky;
  position: sticky;
  top:0;
  z-index: 10;
}

.menhir-nav-bar { margin: 0 0 5px 0 !important; min-height: 90px; padding-top: 20px !important; overflow-y: hidden;}

.size-up {
  transform: scale(1.2);
}

.size-original {
  transform: scale(1.0);
}

.grow { transition: all .2s ease-in-out; }

.stay { display: inline;}

.nav-bar-end-elem { margin: 0 16px 0 0; cursor:pointer;  }
.nav-bar-end-elem span { font-size: 12px; text-transform: uppercase; }
.nav-bar-end-elem span, .nav-bar-end-elem svg { margin-right: 4px }
.nav-bar-end-elem:hover, .nav-bar-end-elem .stay:hover { color: #2C7A7B; }
